import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'step-list',
  template: `
    <ul>
      <li *ngFor="let step of steps">{{step}}</li>
    </ul>
  `,
  styleUrls: ['step-list.component.css']
})
export class StepListComponent implements OnInit {
  steps = [
    '+ Kill Hillock',
    '+ Enter Lioneye\'s Watch',
    '- Speak with Tarkleigh',
    '- Molten Strike'
  ]

  constructor() { }

  ngOnInit() {
  }

}



